import {getCarData} from '../../network/car.js'
import carList from '../../compoments/carList/carList.vue'
export default {
	components:{
		carList
	},
	data() {
		return {
			goods:[]
		}
	},
	methods: {
		getCar(){
			getCarData().then(res=>{
				console.log(res)
				if(res.data.code == 100 ){
					this.goods = res.data.info.map(item=>{
						item.checked = true
						return item
					})
					
				}
			})
		},
		choose(){
			
		},
		toIndex(){
			uni.switchTab({
				url:'/pages/index/index'
			})
		}
	},
	computed:{
		haveGoods(){
			return this.goods.length>0?true:false
		}
	},
	onLoad() {
		this.getCar()
	}
}
